import { ChangeDetectionStrategy, Component } from '@angular/core';
import { InputFieldComponent } from '../../../shared/components/input-field/input-field.component';
import {
  FormBuilder,
  FormGroup,
  ReactiveFormsModule,
  Validators,
} from '@angular/forms';
import { Router } from '@angular/router';
import { path } from '../../../models/routes.model';
import { AuthService } from '../services/auth/auth.service';
import { CommonModule } from '@angular/common';
import { ModalComponent } from '../../../shared/components/modal/modal.component';
import { catchError, finalize, of, tap } from 'rxjs';

@Component({
  selector: 'app-forgot-password',
  standalone: true,
  imports: [
    InputFieldComponent,
    ReactiveFormsModule,
    CommonModule,
    ModalComponent,
  ],
  templateUrl: './forgot-password.component.html',
  styleUrl: './forgot-password.component.scss',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ForgotPasswordComponent {
  public formGroup: FormGroup;
  private PASSWORD_RESET = `/${path.PASSWORD_RESET}`;

  isLoading = false;
  isSentRequest = false;
  isSuccess = false;
  isError = false;
  info: any = {};

  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private _authSevice: AuthService
  ) {
    this.formGroup = this.formBuilder.group({
      email: ['', [Validators.required, Validators.email]],
    });
  }

  onSendRequest(): void {
    const email = this.formGroup.get('email')?.value;

    if (this.formGroup.invalid) {
      this.formGroup.markAllAsTouched();
      return;
    } else {
      this.isLoading = true;

      this._authSevice
        .forgotPassword(email)
        .pipe(
          tap((res) => {
            if (res) {
              this.info = res;
              this.isLoading = false;
              this.isSentRequest = true;
              console.log('res:', res);
            }
          }),
          catchError((err) => {
            this.isLoading = false;
            this.isSentRequest = true;

            this.info = err;
            console.log('error', this.info);
            return of(err);
          }),
          finalize(() => {
            this.isLoading = false;
            this.isSentRequest = true;
            console.log('final isSentRequest:', this.isSentRequest);
          })
        )
        .subscribe();
    }
  }

  onClick() {
    this.isSentRequest = !this.isSentRequest;
    if (this.info.status === 'success') {
      this.router.navigate([this.PASSWORD_RESET]);
    }
  }
}
